import React, { Component } from "react";
import axios from "axios";
import { NavLink } from "react-router-dom";
import "../styles/News.css";

export default class News extends Component {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      items: []
    };
  }

  componentDidMount() {
    axios.get("http://localhost:8080/sak").then(res => {
      const items = res.data;
      console.log(items);
      this.setState({ items });
      this.setState({ isLoaded: true });
    });
  }

  render() {
    const { error, isLoaded, items } = this.state;
    if (error) {
      return <div>Error: {error.message}</div>;
    } else if (!isLoaded) {
      return <div>Loading...</div>;
    } else {
      return (
        <div className="mx-4 my-4">
          <div class="card-columns">{items.map(temp => newsCard(temp))}</div>
        </div>
      );
    }
  }
}

function newsCard(props) {
  return (
    <div class="card">
      <NavLink className="nav-link newsFont" exact to={"/sak/" + props.sak_id}>
        <img class="card-img-top" src={props.bilde} alt="Alt-text"></img>
        <div class="card-body">
          <h5 class="card-title">{props.overskrift}</h5>
          <p class="card-text">{props.innhold}</p>
          <p class="card-text">
            <small class="text-muted">Lagt til: {props.tidspunkt}</small>
          </p>
        </div>
      </NavLink>
    </div>
  );
}
