import React, { Component } from "react";
import Navbar from "./Navbar.js";
import Footer from "./Footer.js";
import axios from "axios";
import Edit from "./Edit";

export default class RegisterForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      overskrift: "",
      innhold: "",
      bilde: "",
      kategori: "Kategori",
      viktighet: 1
    };
  }

  handleChange = event => {
    this.setState({ [event.target.name]: event.target.value });
  };

  // Method for submittiting newscase
  submitForm() {
    console.log("Submitting form");

    axios
      .post("http://localhost:8080/sak", this.state)
      .then(res => {
        console.log(res);
        console.log(res.data);
        alert("Din sak ble lagt til");
        window.location.hash = "";
      })
      .catch(error => {
        console.log(error.response);
      });
  }

  render() {
    return (
      <div>
        <div class="jumbotron jumbotron-fluid">
          <div class="container">
            <h1 class="display-4">Registrer en sak!</h1>
            <p class="lead">
              Her kan du laste opp en nyhetssak til vår nettside.
            </p>
          </div>
        </div>
        <form>
          <div class="form-group mx-5">
            <label for="exampleInputEmail1">Tittel</label>
            <input
              name="overskrift"
              onChange={this.handleChange}
              type="text"
              class="form-control"
              placeholder="Skriv inn tittelen til din sak"
            ></input>
            <small id="emailHelp" class="form-text text-muted">
              Denne vil vises på forsiden.
            </small>
          </div>
          <div class="form-group mx-5">
            <label>Beskrivelse</label>
            <textarea
              name="innhold"
              onChange={this.handleChange}
              type="text"
              class="form-control"
              placeholder="Skriv inn det innholdet du ønsker vist i saken"
              rows="5"
            ></textarea>
          </div>
          <div class="row mx-5">
            <div class="form-group">
              <label for="exampleInputEmail1">Bilde-URL</label>
              <input
                name="bilde"
                onChange={this.handleChange}
                type="text"
                class="form-control"
                placeholder="Lim inn en bilde-url her"
              ></input>
            </div>
            <div class="form-group mx-5">
              <label for="exampleInputEmail1">Kategori</label>
              <div class="dropdown">
                <button
                  class="btn btn-secondary dropdown-toggle"
                  type="button"
                  id="dropdownMenuButton"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  {this.state.kategori}
                </button>
                <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                  <a
                    class="dropdown-item"
                    onClick={() => this.setState({ kategori: "Sport" })}
                  >
                    Sport
                  </a>
                  <a
                    class="dropdown-item"
                    onClick={() => this.setState({ kategori: "Religion" })}
                  >
                    Religion
                  </a>
                  <a
                    class="dropdown-item"
                    onClick={() => this.setState({ kategori: "Kultur" })}
                  >
                    Kultur
                  </a>
                </div>
              </div>
            </div>
            <div class="form-group mx-5">
              <label for="importancyInput">
                Viktighet:&nbsp;
                {this.state.viktighet +
                  " " +
                  (this.state.viktighet == 1
                    ? "Forsidememateriale"
                    : "Kategorispesifikk artikkel")}
              </label>
              <br></br>
              <div class="btn-group btn-group-toggle" data-toggle="buttons">
                <button
                  class="btn btn-secondary active"
                  onClick={() => this.setState({ viktighet: 1 })}
                >
                  <input
                    type="radio"
                    name="options"
                    id="option1"
                    autocomplete="off"
                    checked
                  ></input>
                  1
                </button>
                <button
                  class="btn btn-secondary"
                  onClick={() => this.setState({ viktighet: 2 })}
                >
                  <input
                    type="radio"
                    name="options"
                    id="option2"
                    autocomplete="off"
                  ></input>{" "}
                  2
                </button>
              </div>
            </div>
          </div>

          <button
            type="button"
            onClick={this.submitForm}
            class="btn btn-primary mx-5"
          >
            Submit
          </button>
        </form>
      </div>
    );
  }
}
