import React, { Component } from "react";
import axios from "axios";

export default class Edit extends Component {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      items: [],
      kategori: "Kategori",
      viktighet: 1
    };
  }

  componentDidMount() {
    axios.get("http://localhost:8080/sak").then(res => {
      const items = res.data;
      console.log(items);
      this.setState({ items });
      this.setState({ isLoaded: true });
    });
  }

  submitForm(props) {
    console.log("Submitting form");

    axios
      .put("http://localhost:8080/sak/" + props.sak_id, props)
      .then(res => {
        console.log(res);
        console.log(res.data);
        alert("Din sak ble oppdatert");
      })
      .catch(error => {
        console.log(error.response);
      });
  }

  handleChange = event => {
    this.setState({ [event.target.name]: event.target.value });
  };

  editCard(props) {}

  render() {
    const { error, isLoaded, items } = this.state;
    if (error) {
      return <div>Error: {error.message}</div>;
    } else if (!isLoaded) {
      return <div>Loading...</div>;
    } else {
      return (
        <div className="mx-5">
          <h1>Rediger/Slett sak</h1>
          <div class="accordion" id="accordionExample">
            {items.map(e => (
              <EditCard element={e} />
            ))}
          </div>
        </div>
      );
    }
  }
}

class EditCard extends Component<element> {
  constructor(props) {
    super(props);
    this.state = {
      overskrift: this.props.element.overskrift,
      innhold: this.props.element.innhold,
      bilde: this.props.element.bilde,
      kategori_id: this.props.element.kategori_id,
      viktighet: this.props.element.viktighet,
      sak_id: this.props.element.sak_id
    };
  }

  handleChange = event => {
    this.setState({ [event.target.name]: event.target.value });
  };

  // Method for editing newscase
  submitForm = e => {
    console.log("Editing article form");
    console.log(this.state.sak_id);
    var config = {
      headers: { "Access-Control-Allow-Methods": "POST, PUT, GET, OPTIONS" }
    };
    axios
      .put("http://localhost:8080/sak/" + this.state.sak_id, this.state, config)
      .then(res => {
        console.log(res);
        console.log(res.data);
        alert("Din sak ble redigert til");
      })
      .catch(error => {
        console.log(error.response);
      });
  };

  render() {
    return (
      <div class="card">
        <div class="card-header" id={"headingOne"}>
          <h2 class="mb-0">
            <button
              class="btn btn-link"
              type="button"
              data-toggle="collapse"
              data-target={"#collapse" + this.state.sak_id}
              aria-expanded="true"
              aria-controls="collapseOne"
            >
              {this.state.overskrift}
            </button>
          </h2>
        </div>

        <div
          id={"collapse" + this.state.sak_id}
          class="collapse"
          aria-labelledby="headingOne"
          data-parent="#accordionExample"
        >
          <div class="card-body">
            <form>
              <div class="form-group mx-5">
                <label for="exampleInputEmail1">Tittel</label>
                <input
                  name="overskrift"
                  onChange={this.handleChange}
                  type="text"
                  class="form-control"
                  placeholder="Skriv inn tittelen til din sak"
                  defaultValue={this.state.overskrift}
                ></input>
                <small id="emailHelp" class="form-text text-muted">
                  Denne vil vises på forsiden.
                </small>
              </div>
              <div class="form-group mx-5">
                <label>Beskrivelse</label>
                <textarea
                  name="innhold"
                  onChange={this.handleChange}
                  type="text"
                  class="form-control"
                  placeholder="Skriv inn det innholdet du ønsker vist i saken"
                  rows="5"
                  defaultValue={this.state.innhold}
                ></textarea>
              </div>
              <div class="row mx-5">
                <div class="form-group">
                  <label for="exampleInputEmail1">Bilde-URL</label>
                  <input
                    name="bilde"
                    onChange={this.handleChange}
                    type="text"
                    class="form-control"
                    placeholder="Lim inn en bilde-url her"
                    defaultValue={this.state.bilde}
                  ></input>
                </div>
                <div class="form-group mx-5">
                  <label for="exampleInputEmail1">Kategori</label>
                  <div class="dropdown">
                    <button
                      class="btn btn-secondary dropdown-toggle"
                      type="button"
                      id="dropdownMenuButton"
                      data-toggle="dropdown"
                      aria-haspopup="true"
                      aria-expanded="false"
                    >
                      {this.state.kategori_id == 1
                        ? "Sport"
                        : this.state.kategori_id == 2
                        ? "Religion"
                        : "Kultur"}
                    </button>
                    <div
                      class="dropdown-menu"
                      aria-labelledby="dropdownMenuButton"
                    >
                      <a
                        class="dropdown-item"
                        onClick={() => this.setState({ kategori_id: 1 })}
                      >
                        Sport
                      </a>
                      <a
                        class="dropdown-item"
                        onClick={() => this.setState({ kategori_id: 2 })}
                      >
                        Religion
                      </a>
                      <a
                        class="dropdown-item"
                        onClick={() => this.setState({ kategori_id: 3 })}
                      >
                        Kultur
                      </a>
                    </div>
                  </div>
                </div>
                <div class="form-group mx-5">
                  <label for="importancyInput">
                    Viktighet:&nbsp;
                    {this.state.viktighet +
                      " " +
                      (this.state.viktighet == 1
                        ? "Forsidememateriale"
                        : "Kategorispesifikk artikkel")}
                  </label>
                  <br></br>
                  <div class="btn-group btn-group-toggle" data-toggle="buttons">
                    <button
                      class="btn btn-secondary active"
                      onClick={() => this.setState({ viktighet: 1 })}
                    >
                      <input
                        type="radio"
                        name="options"
                        id="option1"
                        autocomplete="off"
                        checked
                      ></input>
                      1
                    </button>
                    <button
                      class="btn btn-secondary"
                      onClick={() => this.setState({ viktighet: 2 })}
                    >
                      <input
                        type="radio"
                        name="options"
                        id="option2"
                        autocomplete="off"
                      ></input>
                      2
                    </button>
                  </div>
                </div>
              </div>

              <button
                type="button"
                onClick={() => this.submitForm()}
                class="btn btn-primary mx-5"
              >
                Submit
              </button>
            </form>
          </div>
        </div>
      </div>
    );
  }
}
